import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import ScrollToPlugin from 'gsap/ScrollToPlugin';

const postSidebar = () => {
	gsap.registerPlugin(ScrollTrigger);
	gsap.registerPlugin(ScrollToPlugin);

	const $sidebar = document.querySelector('.postSidebar');
	const $socialSidebar = document.querySelector('.postSocials');
	const $section = document.querySelector('.postSection');

	const windowHeight = window.innerHeight;

	const sidebarActiveClass = 'post_content__sidebar_list--active';
	const sidebarFinishClass = 'post_content__sidebar_list--finish';

	const socialActiveClass = 'post_content__social--active';
	const socialFinishClass = 'post_content__social--finish';

	const initSidebar = (el, section, activeClass, finishClass) => {
		const sidebarHeight = el.offsetHeight;
		const sidebarBottomOffset = windowHeight - sidebarHeight;

		ScrollTrigger.matchMedia({
			'(min-width: 1024px)': () => {
				ScrollTrigger.batch(section, {
					start: 'top top',
					end: `bottom+=${sidebarBottomOffset} bottom`,
					onEnter: () => {
						el.classList.add(activeClass);
					},
					onEnterBack: () => {
						el.classList.add(activeClass);
						el.classList.remove(finishClass);
					},
					onLeave: () => {
						el.classList.remove(activeClass);
						el.classList.add(finishClass);
					},
					onLeaveBack: () => {
						el.classList.remove(activeClass);
					},
				});
			},
		});
	};

	if ($section) {
		initSidebar($sidebar, $section, sidebarActiveClass, sidebarFinishClass);
		initSidebar($socialSidebar, $section, socialActiveClass, socialFinishClass);
	}

	// anchors

	const $anchorLinks = document.querySelectorAll('.post_content__sidebar_link');

	$anchorLinks.forEach(item => {
		item.addEventListener('click', (e) => {
			e.preventDefault();
			const href = item.getAttribute('href');
			const topY = document.querySelector(href).getBoundingClientRect().top - document.body.getBoundingClientRect().top;

			gsap.to(window, 0.6, {
				scrollTo: {
					y: topY,
					autoKill: false,
				},
				ease: 'none',
			});
		});
	});

	const $anchors = document.querySelector('.post_content__article').querySelectorAll('h2, h3');
	console.log($anchors);

	$anchors.forEach(item => {
		ScrollTrigger.batch(item, {
			start: 'top top',
			end: 'bottom bottom',
			onEnter: () => {
				setActiveAnchorLink(item);
			},
			onEnterBack: () => {
				setActiveAnchorLink(item);
			},
		});
	});

	const setActiveAnchorLink = (item) => {
		const id = item.getAttribute('id');
		const link = document.querySelector(`[href="#${id}"]`);
		$anchorLinks.forEach(el => el.classList.remove('post_content__sidebar_link--active'));
		link.classList.add('post_content__sidebar_link--active');
	};

	// social link - copy article url
	const socialLinkCopy = document.querySelector('.post_content__social_link--link');
	if (socialLinkCopy) {
		socialLinkCopy.addEventListener('click', (e) => {
			e.preventDefault();
			navigator.clipboard.writeText(window.location.href);
		});
	}
};

export default postSidebar;
